<template>
  <div :class="this.$root.themeHomeChange==='2'?'companyCenterBody':'companyCenterBodyLight'">
    <div class="companyCenterBody-con">
      <div class="companyCenterBodyLeft">
        <div class="left-con">
          <div class="area">
            <p class="detail" @click="$router.push('/regulationTable?index=2-2&actNav=2')">详情&gt;&gt;</p>
            <div class="systemList">
              <div
                class="systemList-con"
                v-for="item in regimeData"
                :key="item.title"
              >
                <p class="titleName-massage">
                  <span class="num">{{ item.num }}</span>
                  <span class="sort el-icon-top">{{ item.rise }}</span>
                </p>
                <p class="titleName">{{ item.title }}</p>
              </div>
            </div>
            <!-- 箭头 -->
            <div class="divide"></div>
            <div class="regionalism">
              <bar-chart
                  v-if="showChart"
                :seriesData="planCharts"
                :xData="rXdata"
                :_zb="dwR"
              ></bar-chart>
            </div>
          </div>
        </div>
      </div>

      <div class="companyCenterBodyRight">
        <div class="right-con">
          <!-- 头部title -->
          <div class="title">
            <p class="titleName">智能检验逻辑：<span>{{jcNum}}</span></p>
            <p class="more" @click="$router.push('/companyCenterManage?index=2-3&actNav=2')">更多&gt;&gt;</p>
          </div>

          <!-- 分析数据 -->
          <div class="analyseList">
            <div class="analyseList-con">
              <div
                class="analyseData"
                v-for="(item, idx) in analyseList"
                :key="idx"
              >
                <p>
                  {{ item.title }}
                </p>
              </div>
            </div>

            <div class="btns">
              <div class="btnsBox">
                <img
                  @click="topClick"
                  src="../../../assets/img/companyCenter/top.png"
                  alt=""
                />
                <img
                  @click="bottomClick"
                  src="../../../assets/img/companyCenter/bottom.png"
                  alt=""
                />
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import barChart from "../../components/companyCenter/companyCenterBody-charts";
import * as echarts from "echarts";
import Interface from "../../../base/mixin/enterprise-manage-center";
export default {
  components: { barChart },
  mixins: [Interface],
  data() {
    return {
      timer: "",
      // 规则制度列表
      regimeData: [
        {
          title: "法律法规",
          num: "238",
          rise: "24%",
        },
        {
          title: "制度规范",
          num: "153",
          rise: "10%",
        },
        {
          title: "内控管理规则",
          num: "300",
          rise: "32%",
        },
        {
          title: "其他",
          num: "268",
          rise: "44%",
        },
      ],

      // 分析数据列表
      analyseList: [
        {
          title: "典型造价合规性分析",
        },
        {
          title: "工程技术管理合规性分析",
        },
        {
          title: "设计变更管理合规性分析",
        },
        {
          title: "典型造价合规性分析",
        },
        {
          title: "工程技术管理合规性分析",
        },
        {
          title: "设计变更管理合规性分析",
        },
        {
          title: "典型造价合规性分析",
        },
        {
          title: "工程技术管理合规性分析",
        },
      ],

      ...{
        planCharts: [],
        rXdata:['办公室 ','合规与监审部','系统规划部','资产管理部','市场营销部','安全监管部','人力资源部','党建工作部','创新与数字化部','财务部'],
        dwR: ["单位：个", ""],
        showChart:true,
        jcNum:''
      },
    };
  },

  created() {
    this.getplanCharts();
    this.analyseList = []
    this.homeRuleData({
      size:7
    }).then((res)=>{
      this.jcNum = res.records+'项'
      this.analyseList = res.rows.map(v=>{
        return {
          title : v.name
        }
      })
    })
  },

  mounted() {
    this.init();
    let tableDom = document.getElementsByClassName("companyCenterBodyRight");
    tableDom[0].onmouseenter = () => {
      this.pause();
    };
    tableDom[0].onmouseleave = () => {
      this.init();
    };
  },

  methods: {
    init() {
      clearInterval(this.timer);
      this.tableMove("bottom");
      this.timer = setInterval(() => {
        this.tableMove("bottom");
      }, 3000);
    },
    pause() {
      clearInterval(this.timer);
    },
    tableMove(direction) {
      // 48px 为表格行高
      if (direction === "top") {
        let flag = 1;
        if (flag) {
          flag = 0;
          let t = document.getElementsByClassName("analyseList-con");
          setTimeout(() => {
            t[0].style.marginTop = "10px";
            t[0].style.marginBottom = "-20px";
            this.analyseList.splice(this.analyseList.length - 1, 1);
          }, 0);
          setTimeout(() => {
            this.analyseList.unshift(this.analyseList[this.analyseList.length - 1]);
            t[0].style.marginTop = "0px";
            t[0].style.marginBottom = "0px";
            flag = 1;
          }, 500);
        }
      } else if (direction === "bottom") {
        let flag = 1;
        if (flag) {
          flag = 0;
          let t = document.getElementsByClassName("analyseList-con");
          setTimeout(() => {
            t[0].style.marginTop = "-10px";
            t[0].style.marginBottom = "20px";
            this.analyseList.splice(0, 1);
          }, 0);
          setTimeout(() => {
            this.analyseList.push(this.analyseList[0]);
            t[0].style.marginTop = "0px";
            t[0].style.marginBottom = "0px";
            flag = 1;
          }, 500);
        }
      }
    },

    topClick() {
      this.tableMove("top");
    },

    bottomClick() {
      clearInterval(this.timer);
      this.tableMove("bottom");
    },

    getplanCharts() {
      this.showChart = false
      this.rXdata = []
      let dataChart = []
      this.regimeData = [
        // {
        //   title: "法律法规",
        //   num: "238",
        //   rise: "24%",
        // },
      ]
      this.homeKnowledgeBarData({

      }).then((res)=>{
        this.regimeData = res.statisticByTypeResults.map(v=>{
          return {
            title : v.knowledgeTypeName,
            num : v.knowledgeCount,
            rise : v.increaseRate*100+'%',
          }
        })
        this.regimeData.push({
          title:'其他',
          num:'0',
          rise:'0%',
        })

        res.statisticByDepartmentResult.forEach(v=>{
          this.rXdata.push(v.department)
          dataChart.push(v.count)
        })
        window.console.log(this.rXdata,'pppp')

        this.planCharts = [
          {
            // name: "规则数",
            type: "bar",
            barWidth: 30,
            data: dataChart,
            itemStyle: {
              normal: {
                label: {
                  show: true,
                  position: "top",
                  textStyle: {
                    color: "#00f8ff",
                  },
                },
                color: new echarts.graphic.LinearGradient(
                    0,
                    1,
                    0,
                    0,
                    [
                      {
                        offset: 0,
                        color: "#ff8000", // 0% 处的颜色
                      },
                      {
                        offset: 1,
                        color: "#f8c288", // 100% 处的颜色
                      },
                    ],
                    false
                ),
              },
            },
          },
          // {
          //   name: "覆盖率",
          //   type: "line",
          //   // symbol: 'circle',//折线点设置为实心点
          //   symbolSize: 12, //折线点的大小
          //   data: [15, 15, 15, 15, 15, 15, 15 ,15, 15, 15],
          //   itemStyle: {
          //     normal: {
          //       color: "#FFEB71",
          //       shadowColor: "#FFEB71",
          //       shadowBlur: 20,
          //       lineStyle: {
          //         width: 4, //折线宽度
          //       },
          //     },
          //   },
          // },
        ];
      }).finally(()=>{
        this.showChart = true
      })
    },
  },

  beforeDestroy() {
    this.pause();
  },
};
</script>
<style lang="scss" scoped>
</style>

